.hero-wr {
    clip-path: polygon(100% 0%, 100% 86%, 63% 100%, 0 86%, 0 0);
    position: relative;
    top: 0;
    z-index: $zindex_heros;
    left: 0;
    &,
    .hero-video {
        height: (100vw)/2.168;
        width: 100%;
        top: 0;
    }
    .hero-video {
        position: absolute;
        z-index: $zindex_heros;
        height: $width_video_height;
        top: -5px;
    }
}